@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Kanit|Lobster|Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.movie-card {
  margin-left: 15px;
  margin-bottom: 15px;
  max-width: 200px;
  width: 300px;
}

.movie-card.card {
  height: 300px;
}

.card-footer-badge {
  font-size: 1rem;
}

.movie-description:hover > p {
  color:white;
}

.movie-description:hover {
  background: black;
  opacity: 0.75;
}

.movie-description {
  color: transparent;
}

.card-border {
  outline: 5px solid #6da5ff; 
}
.navbar-style {
    background-color: #ffcc5c;
}

.comparitron-logo {
    margin: auto 45% auto 35%;
    font-family: 'Lobster', 'Verdana';
    color: #211059;
    font-size: 3em;
    text-shadow: 2px 4px rgba(66, 31, 107, 0.2);
}

.select-text {
    font-family: 'Josefin Sans', sans-serif;
    margin-top: 15px;
    font-size: 20px;
    font-weight: "bold";
    color: #211059;
}

.review-selection, .movie-selection {
    border-radius: 15px;
  border-color: transparent;
  border-width: 1px;
  border-style: solid;
  background-color:#fff;
  color: #211059;
  font-family: 'Kanit', sans-serif;
  font-size: 18px;
  outline: none;
  width: 100%;
  margin-top: 5px;
  cursor: pointer;
  outline: none !important;
  text-align: center;
  padding-left: 10px;
}

.movie-selection {
    text-align: left;
}

/* width */
.scroll::-webkit-scrollbar {
    width: 10px;
}

/* Track */
.scroll::-webkit-scrollbar-track {
    background: #c9ddff;
}

/* Handle */
.scroll::-webkit-scrollbar-thumb {
    background: #6da5ff;
}

/* Handle on hover */
.scroll::-webkit-scrollbar-thumb:hover {
    background: #4b60ea;
}

